export default {
    data() {
        return {
            currentTab: 'vcard',
            cardList: [],
            venueArr: []
        }
    },
    created() {
        this.getOrderList()
    },
    filters: {
        //paid_state：支付状态，-1：支付失败，0：未支付，1：预支付，10：已支付
        payText (val) {
            let text = ''
            switch (val) {
                case -1:
                    text = '支付失败'
                    break;
                case 0:
                    text = '未支付'
                    break;
                case 1:
                    text = '预支付'
                    break;
                case 10:
                    text = '已支付'
                    break;            
                default:                    
                    text = '未支付'
                    break;
            }
            return text
        }
    },
    methods: {
        changeTabs (type) {
            this.currentTab = type
            this.getOrderList()
        },
        async getOrderList () {
            const params  = {
                page: 1,
                pageSize: 100,
                type: this.currentTab === 'venue' ? 0 : 1
            }
            const res = await this.$api.getOrderList(params)
            if (res.code === 0) {
                if (this.currentTab === 'venue') {
                    this.venueArr = res.data.data
                } else {                    
                    this.cardList = res.data.data
                }
            }
        }
    },
}
